.flex {
    display: flex;
}

.space-between {
    justify-content: space-between;
}

.space-evenly{
    justify-content: space-evenly;
}

.space-around {
    justify-content: space-around;
}

.align-center {
    align-items: center;
}

.justify-center {
    justify-content: center;
}

.justify-start{
    justify-content: start;
}

.column {
    flex-direction: column;
}

.wrap {
	flex-wrap: wrap;
}

.start {
    justify-content: start;
}

.clean-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.text-center {
    text-align: center;
}

.width-all {
    width: 100%;
}

.place-holder {
    width: 100%;
    height: 100%;
    background-color: rgb(159, 226, 159);
    display: flex;
    align-items: center;
    justify-content: center;
}

.absolute {
    position: absolute;
}

.relative {
    position: relative;
}

.grid {
    display: grid;
}

.fit-content {
    width: fit-content;
}

.pointer{
    cursor: pointer;
    &:hover{
        cursor: pointer;
    }
}

.img-dimer {
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.15;
    z-index: 3;
}

.row-reverse{
    flex-direction: row-reverse;
}

.ratio-16-9 { @include aspect-ratio(100, 42); }

.ratio-square { @include aspect-ratio(100, 100); }

.ratio-card { @include aspect-ratio(100, 80); }

.ratio-poster { @include aspect-ratio(100, 150); }


.container{
    margin: 0 auto;
    margin-right:30px;
    margin-left:30px;
}